html, body {
  margin: 0;
  padding: 0;
  z-index: -10;
  font-family: "Roboto", Arial, sans-serif;
}

body {
  z-index: 0;
}

.promoSection {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  color: #ffffff;
}
.promoSection .background {
  margin: 0;
  background-size: cover;
  background-position: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: -10;
  filter: brightness(0.3);
}
.promoSection .content {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
}
.promoSection .content h1 {
  font-weight: 600;
  font-size: 104px;
}
.promoSection .content p {
  font-size: 20px;
  font-weight: 300;
  margin: 0;
  text-align: center;
}

.aboutSection {
  background: #ffffff;
  z-index: 10;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.aboutSection .blocksUl {
  background: #ffffff;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 210px 0 90px 0;
  gap: 60px;
  width: 100%;
  justify-content: center;
  align-items: center;
  z-index: 100;
  box-sizing: border-box;
}
.aboutSection .blocksUl .block {
  display: flex;
  flex-direction: column;
  padding: 60px;
  box-sizing: border-box;
  background: #f5f5f5;
  width: calc(100% - 40px);
  margin: 0 auto;
  box-sizing: border-box;
  max-width: 880px;
}
.aboutSection .blocksUl .block h3 {
  margin: 0 0 35px 0;
  padding: 0;
  font-size: 28px;
  font-weight: 600;
}
.aboutSection .blocksUl .block p {
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-weight: 300;
  line-height: 31px;
}
.aboutSection .whyBox {
  background: #ffffff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 105px 20px 150px 20px;
  width: 100%;
  justify-content: center;
  align-items: center;
  z-index: 100;
  box-sizing: border-box;
}
.aboutSection .whyBox > h2 {
  margin: 0 0 40px 0;
  font-weight: 600;
  font-size: 42px;
  text-align: center;
}
.aboutSection .whyBox > p {
  margin: 0;
  font-weight: 300;
  font-size: 22px;
  max-width: 560px;
  text-align: center;
}
.aboutSection .whyBox ul {
  margin: 0;
  padding: 90px 0 0 0;
  list-style: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 60px;
}
.aboutSection .whyBox .whyItem {
  margin: 0 20px;
  width: calc(100% - 40px);
  max-width: 700px;
  display: grid;
  grid-template-areas: "icon header" "icon info";
  grid-template-columns: auto 1fr;
  column-gap: 30px;
  row-gap: 14px;
  align-items: center;
}
.aboutSection .whyBox .whyItem img {
  height: 65px;
  width: 65px;
  grid-area: icon;
}
.aboutSection .whyBox .whyItem p {
  grid-area: header;
  margin: 0;
  font-weight: 600;
  font-size: 20px;
}
.aboutSection .whyBox .whyItem span {
  grid-area: info;
}

.howSection {
  position: relative;
  z-index: 10;
}
.howSection .howBox {
  padding: 135px 0 165px 0;
  background: #f2f2f2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.howSection .howBox h2 {
  font-size: 42px;
  font-weight: 600;
  margin: 0 0 40px 0;
  text-align: center;
}
.howSection .howBox ul {
  max-width: 1200px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 60px;
  flex-wrap: wrap;
  margin: 50px 0 0 0;
}
.howSection .howBox ul .howItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 320px;
}
.howSection .howBox ul .howItem img {
  height: 80px;
  width: 80px;
  margin-bottom: 25px;
}
.howSection .howBox ul .howItem h3 {
  font-size: 30px;
  margin: 12px 0 25px 0;
  font-weight: 600;
  text-align: center;
}
.howSection .howBox ul .howItem p {
  font-size: 18px;
  font-weight: 300;
  text-align: center;
}

.helpSection {
  position: relative;
  background: rgb(85, 85, 85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  flex-direction: column;
  padding: 135px 20px 165px 20px;
}
.helpSection h2 {
  color: #ffffff;
  font-size: 57px;
  font-weight: 600;
  text-align: center;
  margin: 0 0 40px 0;
}
.helpSection > p {
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  text-align: center;
  max-width: 660px;
}
.helpSection ul {
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 90px 0 0 0;
  gap: 60px;
}
.helpSection ul .helpItem {
  margin: 0 20px;
  width: calc(100% - 40px);
  max-width: 700px;
  display: grid;
  grid-template-areas: "icon header" "icon info";
  grid-template-columns: auto 1fr;
  column-gap: 30px;
  row-gap: 14px;
}
.helpSection ul .helpItem span {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
  grid-area: icon;
  background: #ffffff;
  border-radius: 50%;
  color: #000000;
  font-weight: 600;
}
.helpSection ul .helpItem h3 {
  grid-area: header;
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  color: #ffffff;
}
.helpSection ul .helpItem p {
  grid-area: info;
  color: #ffffff;
  margin: 0;
}

.feedbackSection {
  background: #efefef;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(100% - 40px);
  z-index: 100;
  height: 100vh;
  padding: 0 20px;
}
.feedbackSection h2 {
  width: calc(100% - 40px);
  max-width: 660px;
  font-size: 48px;
  text-align: center;
  font-weight: 600;
}
.feedbackSection button {
  background: #000000;
  width: 200px;
  height: 55px;
  border-radius: 30px;
  padding: 0;
  border: none;
  color: #ffffff;
  font-weight: 600;
  margin: 10px 0 50px 0;
  cursor: pointer;
}
.feedbackSection a {
  text-decoration: none;
  color: #000000 !important;
}
.feedbackSection a:visited {
  color: #555555;
}
.feedbackSection ul {
  list-style: none;
  display: flex;
  gap: 10px;
  padding: 0;
  margin: 30px 0 0 0;
}
.feedbackSection ul img {
  height: 27px;
  width: 27px;
}

@media all and (max-width: 1024px) {
  .promoSection h1 {
    font-size: 70px !important;
  }
  .promoSection p {
    font-size: 18px !important;
  }
  .aboutSection .blocksUl .block {
    padding: 40px !important;
  }
  .aboutSection .blocksUl .block h3 {
    font-size: 26px !important;
  }
  .aboutSection .blocksUl .block p {
    font-size: 18px !important;
    line-height: 26px !important;
  }
  .aboutSection .whyBox > h2 {
    font-size: 38px !important;
  }
  .aboutSection .whyBox p {
    font-size: 22px !important;
  }
  .aboutSection .whyBox .whyItem p {
    font-size: 18px !important;
  }
  .aboutSection .whyBox .whyItem span {
    font-size: 16px !important;
  }
  .howSection .howBox h2 {
    font-size: 18px !important;
  }
  .howSection .howBox .howItem h3 {
    font-size: 28px !important;
  }
  .howSection .howBox .howItem p {
    font-size: 16px !important;
  }
  .helpSection h2 {
    font-size: 38px !important;
  }
  .helpSection > p {
    font-size: 22px !important;
  }
  .helpSection .helpItem p {
    font-size: 18px !important;
  }
  .helpSection .helpItem span {
    font-size: 16px !important;
  }
  .feedbackSection h2 {
    font-size: 40px !important;
  }
}
@media all and (max-width: 640px) {
  .promoSection h1 {
    font-size: 34px !important;
  }
  .promoSection p {
    font-size: 16px !important;
  }
  .aboutSection .blocksUl .block {
    padding: 20px !important;
  }
  .aboutSection .blocksUl .block h3 {
    font-size: 24px !important;
  }
  .aboutSection .blocksUl .block p {
    font-size: 16px !important;
    line-height: 24px !important;
  }
  .aboutSection .whyBox > h2 {
    font-size: 30px !important;
  }
  .aboutSection .whyBox p {
    font-size: 20px !important;
  }
  .aboutSection .whyBox .whyItem p {
    font-size: 16px !important;
  }
  .aboutSection .whyBox .whyItem span {
    font-size: 14px !important;
  }
  .howSection .howBox h2 {
    font-size: 28px !important;
  }
  .howSection .howBox .howItem h3 {
    font-size: 24px !important;
  }
  .howSection .howBox .howItem p {
    font-size: 14px !important;
  }
  .helpSection h2 {
    font-size: 28px !important;
  }
  .helpSection > p {
    font-size: 18px !important;
  }
  .helpSection .helpItem p {
    font-size: 16px !important;
  }
  .helpSection .helpItem span {
    font-size: 14px !important;
  }
  .feedbackSection h2 {
    font-size: 30px !important;
  }
}

